import React,{useState, useEffect} from 'react';
import axios from 'axios';


function SiteIntro(){

	const [logo, setLogo] = useState('');
	useEffect(()=>{
		const url = localStorage.getItem('url');
		axios.get(url+'logo/').then(res=>{
			setLogo(res.data[0].Logo);
		}).catch(err=>console.error(err))
	},[])
	
	return(
		<div className='SiteIntro' style={{'background':'purple'}}>
			<img className='logo' src={logo} alt='logo'/>
			<h1>Save your Time</h1>
			<p>Time is money and by booking your time,you can save your money.</p>
			<p>This is servire booking site. You can do many more stuff using this let us explore.</p>
			<a href='#'>Knonw more about us.</a>
		</div>
	);
}

export default SiteIntro;
